import React from 'react';
import Card from 'react-bootstrap/Card'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import '../App.css'
import { operatingSystems, security, softwareTroubleshooting, operationalProcedures } from '../data/1002'

class Content1002 extends React.Component {
    constructor(){
        super();
        this.determineGradient = this.determineGradient.bind(this)
    }


    determineGradient(item){
        let gradientType = 'myCardImage';

        if (this.props.width >= 992){
            if (item.key%3===1){gradientType = 'gradient1'}
            else if (item.key%3===2){gradientType = 'gradient2'}
            else {gradientType = 'gradient3'}
        }
        else if (this.props.width < 992 && this.props.width >= 768){
            if (item.key%2 === 1){gradientType = 'gradientLeft'}
            else { gradientType = 'gradientRight'}
        }
        else {gradientType = 'myCardImage'}

        return(gradientType)
    }

    render(){
        let h5style = {marginTop: '1em'}
        const operatingSystemsLessonCards = operatingSystems.map((item)=>{
            return(
            <Col xs={12} md={6} lg={4} className="mt-3" key={item.key}>
                <Card className="myCard">
                    <Card.Img variant="top" className={this.determineGradient(item)}/>
                    <Card.Body>
                        <Card.Title style={{fontSize: '14pt', position: 'absolute', top: '20px', color: 'white'}}>{item.title}</Card.Title>
                        <Card.Text style={{fontSize: '12pt'}}>
                            {item.description}
                        </Card.Text>
                        <Card.Text>
                            <a href={item.teacherNotes}>Teacher Notes</a><br/>
                            <a href={item.studentNotes}>Student Notes</a>
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        )})

        const securityLessonCards = security.map((item)=>(
            <Col xs={12} md={6} lg={4} className="mt-3" key={item.key}>
                <Card className="myCard">
                    <Card.Img variant="top" className={this.determineGradient(item)}/>
                    <Card.Body>
                        <Card.Title style={{fontSize: '14pt', position: 'absolute', top: '20px', color: 'white'}}>{item.title}</Card.Title>
                        <Card.Text style={{fontSize: '12pt'}}>
                            {item.description}
                        </Card.Text>
                        <Card.Text>
                            <a href={item.teacherNotes}>Teacher Notes</a><br/>
                            <a href={item.studentNotes}>Student Notes</a>
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ))

        const softwareTroubleshootingLessonCards = softwareTroubleshooting.map((item)=>(
            <Col xs={12} md={6} lg={4} className="mt-3" key={item.key}>
                <Card className="myCard">
                    <Card.Img variant="top" className={this.determineGradient(item)}/>
                    <Card.Body>
                        <Card.Title style={{fontSize: '14pt', position: 'absolute', top: '20px', color: 'white'}}>{item.title}</Card.Title>
                        <Card.Text style={{fontSize: '12pt'}}>
                            {item.description}
                        </Card.Text>
                        <Card.Text>
                            <a href={item.teacherNotes}>Teacher Notes</a><br/>
                            <a href={item.studentNotes}>Student Notes</a>
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ))

        const operationalProceduresLessonCards = operationalProcedures.map((item)=>(
            <Col xs={12} md={6} lg={4} className="mt-3" key={item.key}>
                <Card className="myCard">
                    <Card.Img variant="top" className={this.determineGradient(item)}/>
                    <Card.Body>
                        <Card.Title style={{fontSize: '14pt', position: 'absolute', top: '20px', color: 'white'}}>{item.title}</Card.Title>
                        <Card.Text style={{fontSize: '12pt'}}>
                            {item.description}
                        </Card.Text>
                        <Card.Text>
                            <a href={item.teacherNotes}>Teacher Notes</a><br/>
                            <a href={item.studentNotes}>Student Notes</a>
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Col>
        ))


        if (this.props.navitem === 'navitem6'){return(<Row>{operatingSystemsLessonCards}</Row>)}
        else if (this.props.navitem === 'navitem7'){return(<Row>{securityLessonCards}</Row>)}
        else if (this.props.navitem === 'navitem8'){return(<Row>{softwareTroubleshootingLessonCards}</Row>)}
        else if (this.props.navitem === 'navitem9'){return(<Row>{operationalProceduresLessonCards}</Row>)}
        else {return(
            <React.Fragment>
                <h5 style={h5style}>Operating Systems</h5>
                <Row>{operatingSystemsLessonCards}</Row>
                <h5 style={h5style}>Security</h5>
                <Row>{securityLessonCards}</Row>
                <h5 style={h5style}>Software Troubleshooting</h5>
                <Row>{softwareTroubleshootingLessonCards}</Row>
                <h5 style={h5style}>Operational Procedures</h5>
                <Row>{operatingSystemsLessonCards}</Row>
            </React.Fragment>
        )}
    }
}

export default Content1002;