import React from 'react';
import './App.css';
import Footer from './components/Footer'
import Content1001 from './components/Content1001'
import Nav1001 from './components/Nav1001'
import Nav1002 from './components/Nav1002'
import headerGraphic from './images/headergraphic.png'
import CourseDescription from './components/CourseDescription'
import Content1002 from './components/Content1002'

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      navitem: 'none',
      content: 'none'
    }
    this.callBackFunction = this.callBackFunction.bind(this)
    this.selectContent = this.selectContent.bind(this)
  }

  callBackFunction(navData){
    this.setState({navitem: navData})
  }

  handleResize = () => this.setState({
    windowHeight: window.innerHeight,
    windowWidth: window.innerWidth
  });

  componentDidMount() {
    this.handleResize();
    window.addEventListener('resize', this.handleResize)
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize)
  }

  selectContent(item){
    this.setState({content: item, navitem: 'none'});
  }


  render(){

    let nav;
    let content;
    let style = 'hr1';
    if (this.state.content === '1001'){
      nav = <Nav1001 parentCallBack = {this.callBackFunction} navSelection = {this.state.navitem} width = {this.state.windowWidth}/>;
      content = <Content1001 navitem={this.state.navitem} width = {this.state.windowWidth}/>
      style = 'hr2' ;
    }
    else if (this.state.content === '1002'){
      nav = <Nav1002 parentCallBack = {this.callBackFunction} navSelection = {this.state.navitem} width = {this.state.windowWidth}/>;
      content = <Content1002 navitem={this.state.navitem} width = {this.state.windowWidth}/>;
      style = 'hr3';
    }
    else{
      nav = <img style={{width: '100%'}} src={headerGraphic} alt="headerimage"/>;
      content = <CourseDescription/>
    }

    return (
      <div style={{marginLeft: '10px', marginRight: '10px'}}>
        <ul className="supernavcontainer">
          <li className="one supernavitem" onClick={this.selectContent.bind(this, 'none')}>Course Information</li>
          <li className="two supernavitem" onClick={this.selectContent.bind(this, '1001')}>Content for 1001</li>
          <li className="three supernavitem" onClick={this.selectContent.bind(this, '1002')}>Content for 1002</li>
          <hr className={style}/>
        </ul>
        {nav}
        {content}
        <Footer />
      </div>
    );
  }
}

export default App;
