import React from 'react';
import '../App.css';
import operatingSystems from '../images/A+ Thing 2-02.png'
import security from '../images/A+ Thing 2-03.png'
import softwareTroubleshooting from '../images/A+ Thing 2-04.png'
import operationalProcedures from '../images/A+ Thing 2-05.png'
import CardGroup from'react-bootstrap/CardGroup'
import Card from 'react-bootstrap/Card'
import ListGroup from 'react-bootstrap/ListGroup'


class Nav1002 extends React.Component {
    constructor(){
        super();
        this.handleClick = this.handleClick.bind(this)
    }

    handleClick(data){
        this.props.parentCallBack(data)
    }
    
    render(){
        let navSelection = this.props.navSelection;
        let id = ["myCardGroupImage6","myCardGroupImage7","myCardGroupImage8","myCardGroupImage9"]
        let style1 = {textAlign: 'center'};
        let style2 = {textAlign: 'center'};
        let style3 = {textAlign: 'center'};
        let style4 = {textAlign: 'center'};
        
        if (navSelection === 'navitem6'){
            id = ["myCardGroupImage6","inactive2","inactive3","inactive4"]
            style1 = {textAlign: 'center', background: '#333333', color: 'white'}
        }
        if (navSelection === 'navitem7'){
            id = ["inactive1","myCardGroupImage7","inactive3","inactive4"]
            style2 = {textAlign: 'center', background: '#333333', color: 'white'}
        }
        if (navSelection === 'navitem8'){
            id = ["inactive1","inactive2","myCardGroupImage8","inactive4"]
            style3 = {textAlign: 'center', background: '#333333', color: 'white'}
        }
        if (navSelection === 'navitem9'){
            id = ["inactive1","inactive2","inactive3","myCardGroupImage9"]
            style4 = {textAlign: 'center', background: '#333333', color: 'white'}
        }


        if (this.props.width < 576) {
            return(
                <Card>
                    <ListGroup style={{cursor: 'pointer'}}>
                        <ListGroup.Item onClick = {this.handleClick.bind(this,'navitem6')} style={style1}><strong>Operating Systems</strong></ListGroup.Item>
                        <ListGroup.Item onClick = {this.handleClick.bind(this,'navitem7')} style={style2}><strong>Security</strong></ListGroup.Item>
                        <ListGroup.Item onClick = {this.handleClick.bind(this,'navitem8')} style={style3}><strong>Software Troubleshooting</strong></ListGroup.Item>
                        <ListGroup.Item onClick = {this.handleClick.bind(this,'navitem9')} style={style4}><strong>Operational Procedures</strong></ListGroup.Item>
                    </ListGroup>
                </Card>
            )
        }
        else{
            return(
                <CardGroup className="myCardGroup">
                    <Card className="myCardGroupCard" onClick = {this.handleClick.bind(this,'navitem6')}>
                        <Card.Img variant="top" className="myCardGroupImage" id={id[0]} src={operatingSystems} alt="Mobile Devices"/>
                    </Card>
                    <Card className="myCardGroupCard" onClick = {this.handleClick.bind(this,'navitem7')}>
                        <Card.Img variant="top" className="myCardGroupImage" id={id[1]}src={security} alt="Networking"/>
                    </Card>
                    <Card className="myCardGroupCard" onClick = {this.handleClick.bind(this,'navitem8')}>
                        <Card.Img variant="top" className="myCardGroupImage" id={id[2]} src={softwareTroubleshooting} alt="Hardware"/>
                    </Card>
                    <Card className="myCardGroupCard" onClick = {this.handleClick.bind(this,'navitem9')}>
                        <Card.Img variant="top" className="myCardGroupImage" id={id[3]} src={operationalProcedures} alt="Virtualization"/>
                    </Card>
                </CardGroup>
            )
        }
    }
}

export default Nav1002;